
.navigation{
  height: 70px;
  /* background-color: #64b7f6; */
}

.navimg {
    height: 75px;
    width: 75px;  
    margin-left:70px;
    margin-right: 0px;
  
  
    
}
.textsec{
  padding-top: 10px;
  margin-left: -280px;
  text-align: left;
 
}
.logotext{
  color: #1a8ae5;
  font-weight:bold;
  font-size: larger;
  text-shadow: 3pt cyan;
  text-transform: uppercase;
}
.logop{
  color:#64b7f6;
  font-size: small;
  text-align: center;
  font-kerning: 2pt;
}

.navbar-toggler-icon {
  /* color:black; */
  background-color: #64b7f6;
  outline: 1px solid #1a8ae5;
  color: aliceblue;
}
.navbar-nav > ul {
  margin-top: 30%;
}

.navbar-nav > ul > li > a.active {
  color: #1a8ae5;
  text-align: right;
  font-size: 18px;
}

.navbar-nav > .active > a {
  color: #1a8ae5;
  font-size: 16px;
}
.navbar-nav > .active > a:hover {
  color: #64b7f6;
  font-size: 18;
  cursor: pointer;
  padding-left: 15px;
  transition: all 0.52s ease;
}
@media only screen and (max-width: 600px) {
  .navigation{
      height: auto;
      /* background-color: #64b7f6; */
  }
  .navimg {
      height: 80px;
      width: 80px;
      margin-top:0px;
      margin-left:10px;
    
  }
  .textsec{
      padding-top: 10px;
      margin-left: 0px;
      visibility:hidden;
  }
  
 
 }
